nav{
  width: 850px;
  ul{
    width: 100%;
    @include middleAll;
    justify-content: space-between;
    .nav__item{
      @include navLinkStyles(#000)
    }
    .nav__item:last-child{
      @include middleAll;
      svg{
        margin-left: 20px;
        .shp1{
          fill:$headingColor;
        }
      }
      svg:first-child{
        margin:0;
      }

    }
  }
}