//*************************** Section-Promo ***************************//
.section__promo{
  min-height: 750px;
  background: url('../images/promo__bg.png') no-repeat;
  background-size: cover;
  .section__container{
    height: 370px;
    @include middleAll;
    flex-direction: column;
    h1{
      @include textStyles(90px,'YesevaOneRegular');
      text-align: center;
      font-weight: normal;
      line-height: 1.3;
    }
    svg{
      position: absolute;
      top: -760px;
      left: 50%;
      transform: translateX(-50%);
      width: 230px;
    }
    .promo__btns{
      margin-top: 80px;
      width: 500px;
      @include middleAll;
      justify-content: space-between;
      .promo__btn{
        width: 230px;
        height: 60px;
        cursor: pointer;
        position: relative;
        z-index: 2;
        @include textStyles(18px,$LatoRegular)
      }
      .promo__btn:first-child{
        background: url('../images/promoBtn__bgBlack.png') no-repeat;
        background-size: cover;
      }
      .promo__btn:last-child{
        background: url('../images/promoBtn__bgWhite.png') no-repeat;
        background-size: cover;
      }
    }
  }
}

//*************************** Section-About ***************************//
.section__aboutUs{
  min-height: 710px;
  position: relative;
  z-index: 2;
  &:before{
    content: '';
    width: 100%;
    height: 710px;
    position: absolute;
    z-index: -1;
    background: url('../images/aboutUs__bg.png') no-repeat;
    background-size: cover;
  }
  &:after{
    content: '';
    width: 100%;
    height: 710px;
    position: absolute;
    z-index: 2;
    background: url('../images/aboutUs__bg1.png') no-repeat;
    background-size: cover;
  }

  .section__container{
    @include middleAll;
    height: 510px;
    justify-content: space-evenly;
    .aboutUs__left{
      width: 430px;
      height: 100%;
      text-align: center;
      position: relative;
      h2{
        @include textStyles(36px,$YesevaOneRegular,$headingColor);
      }
      svg{
        position: absolute;
        top: -1040px;
        left: 50%;
        transform: translateX(-50%);
        width: 230px;
      }
      p{
        @include textStyles(16px,$LatoRegular,$descColor);
        margin-top: 70px;
        margin-bottom: 45px;
      }
      img{
        margin-left: 75px;
      }
    }
    .aboutUs__right{
      width: 625px;
      min-height: 625px;
      background: url('../images/aboutUs__dish.png') no-repeat;
      background-size: cover;
    }
  }
}